import { Component, OnInit, TemplateRef, OnDestroy } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';
import { DataTableResource } from 'angular7-data-table';

import { LanguageService } from '../../services/language.service';
import { Subscription } from 'rxjs';

import { ShippingsService } from './shippings.service';

import {
  ExportAsService,
  ExportAsConfig,
  SupportedExtensions,
} from 'ngx-export-as';

@Component({
  selector: 'app-shippings',
  templateUrl: './shippings.component.html',
  styleUrls: ['./shippings.component.scss'],
})
export class ShippingsComponent implements OnInit, OnDestroy {
  itemResource;
  items = [];
  itemCount = 0;
  limit = 20;
  limits = [20, 25, 50];
  offset = 1;
  tableElements = [];

  modalRef: any;

  private langSub: Subscription;
  currentLang;
  arabic: boolean;
  constructor(
    private shippingsService: ShippingsService,
    private modalService: BsModalService,
    private languageService: LanguageService,
    private toastr: ToastrService,
    private spinner: NgxSpinnerService,
    private exportAsService: ExportAsService
  ) {
    this.langSub = this.languageService.currentLanguage.subscribe((lang) => {
      this.currentLang = lang;
      if (this.currentLang && this.currentLang === 'ar') {
        this.arabic = true;
      } else {
        this.arabic = false;
      }
    });
  }

  ngOnDestroy(): void {
    this.langSub.unsubscribe();
  }

  ngOnInit(): void {
    this.spinner.show();

    this.shippingsService.getAll();

    this.shippingsService.shippings.subscribe(async (data) => {
      if (data) {
        this.items = data;

        this.itemResource = new DataTableResource(data);
        this.itemCount = await this.itemResource.count();
        this.reloadItems({
          offset: 0,
          limit: this.limit,
        });
      }
    });

    this.spinner.hide();
  }

  async reloadItems(event) {
    if (this.itemResource) {
      this.tableElements = await this.itemResource.query(event);
    }
  }

  async FilterRecords(FilterRecord) {
    this.tableElements = this.items;
    if (FilterRecord !== '') {
      this.tableElements = this.items.filter(
        (item) =>
          item.from.toLowerCase().includes(FilterRecord) ||
          item.to.toLowerCase().includes(FilterRecord)
      );
    }

    this.itemResource = new DataTableResource(this.tableElements);
    this.itemCount = await this.itemResource.count();
    this.reloadItems({
      offset: 0,
      limit: this.limit,
    });
  }

  itemId;
  openModal(template: TemplateRef<any>, id) {
    this.itemId = id;
    this.modalRef = this.modalService.show(template);
  }

  deleteElement() {
    this.spinner.show();

    this.modalRef.hide();
    this.shippingsService.deleteOne(this.itemId).subscribe(
      (res) => {
        console.log(res);
        this.toastr.info(res['message'], 'Info');
        this.items = [];

        this.shippingsService.getAll();
      },
      (error) => {
        console.log(error);
        this.toastr.error(error.error.message, 'Error');
        // this.error.next(error.error.message);
      }
    );

    this.spinner.hide();
  }

  exportAsConfig: ExportAsConfig = {
    type: 'pdf',
    elementIdOrContent: 'my-table',
    options: {
      jsPDF: {
        orientation: 'landscape',
      },
    },
  };
  exportAs(type: SupportedExtensions, opt?: string) {
    this.exportAsConfig.type = type;
    if (opt) {
      this.exportAsConfig.options.jsPDF.orientation = opt;
    }
    this.exportAsService
      .save(this.exportAsConfig, 'Shippings')
      .subscribe(() => {
        // save started
      });
    this.exportAsService.get(this.exportAsConfig).subscribe((content) => {
      console.log(content);
    });
  }
}
